html,
body,
#root,
#app-root,
#map {
  height: 100%;
}
body {
  margin: 0;
  font-family:
  Roboto Slab, Arial, sans-serif, -apple-system, BlinkMacSystemFont;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family:
    source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
a {
  color: #3375CC;
}